.progress{
    height: 10px;
  }

  .progress-bar-striped{
      background-image: -webkit-linear-gradient(45deg,rgba(78, 3, 3, 0) 25%,transparent 25%,#ff2e2e00 50%,rgba(255,255,255,.15) 50%,rgba(0, 0, 0, 0) 60%,transparent 75%,transparent) !important;
      background-image: linear-gradient(45deg,rgba(78, 3, 3, 0) 25%,transparent 25%,#ff2e2e00 50%,rgba(255,255,255,.15) 50%,rgba(0, 0, 0, 0) 60%,transparent 75%,transparent) !important;
      background-size: 1rem 1rem !important;
  }

  .progress-bar {
    background: #222;
    width: 0;
    -webkit-animation: progress 1.5s ease-in-out forwards;
            animation: progress 1.5s ease-in-out forwards;
  }
  .progress-bar .title {
    opacity: 0;
    -webkit-animation: show 0.35s forwards ease-in-out 0.5s;
            animation: show 0.35s forwards ease-in-out 0.5s;
  }
  
  @-webkit-keyframes progress {
    from {
      width: 0;
    }
    to {
      width: 100%;
    }
  }
  
  @keyframes progress {
    from {
      width: 0;
    }
    to {
      width: 100%;
    }
  }
  @-webkit-keyframes show {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  @keyframes show {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }


